const db = wx.cloud.database();
export default {
	data() {
		return {
			tableData: {},
			delete: false
		}
	},
	onLoad(option){
		db.collection('appointment').where({_id: option._id}).get().then(res =>{
			this.tableData = res.data[0]
			console.log(this.tableData)
		})
	},
	methods:{
		handleDelete(){
			uni.showModal({
			    title: '提示',
			    content: '确认取消预约？',
			    success:(res) =>{
			        if (res.confirm) {
			            console.log('用户点击确定');
						wx.cloud.callFunction({
						  name: 'removeAppoin',
						  data: {
						    _id: this.tableData._id
						  },
						})
						uni.showToast({
						    title: '取消预约成功',
						    duration: 1500
						})
						setTimeout(()=>{
							uni.switchTab({
							    url: '/pages/user/user'
							});
						},1500)
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		deleteAppoin(){
			db.collection('appointment').where({_id: this.tableData._id}).remove().then(res =>{
				console.log(this.tableData)
				uni.showToast({
				    title: '取消预约成功',
				    duration: 2000
				})
				uni.navigateBack({
				    delta: 1
				})
			})
		}
	}
}
